import React from 'react';
import { Card, Button, Form, Modal, message} from 'antd'
import Utils from '../../util/utils';
import axios from '../../axios'
import BaseForm from '../../components/BaseForm'
import ETable from '../../components/ETable';
const FormItem = Form.Item;
export default class Order extends React.Component{
    state  = {
        orderInfo:{},
        orderConfirmVisble:false
    }
    params = {
        page: 1
    }
    formList = [
        {
            type:'SELECT',
            label:'',
            field:'city',
            placeholder:'',
            initialValue:'1',
            width:80,
            list: [{ id: '0', name: '' }, { id: '1', name: '' }, { id: '2', name: '' }, { id: '3', name: '' }]
        },
        {
            type: ''
        },
        {
            type: 'SELECT',
            label: '',
            field:'order_status',
            placeholder: '',
            initialValue: '1',
            width: 80,
            list: [{ id: '0', name: '' }, { id: '1', name: '' }, { id: '2', name: '' }]
        }
    ]
    componentDidMount(){
        this.requestList()
        console.log('order',this.state)
    }

    handleFilter = (params)=>{
        this.params = params;
        this.requestList();
    }
    requestList = ()=>{
        // let _this = this;
        axios.requestList(this,'/orderlist',this.params,true);
        
        // axios.ajax({
        //     url:'/orderlist',
        //     data:{
        //         params: this.params
        //     }
        // }).then((res)=>{
        //     let list = res.result.item_list.map((item, index) => {
        //         item.key = index;
        //         return item;
        //     });
        //     this.setState({
        //         list
        //     })
        // })
    }
    // 
    handleConfirm = ()=>{
        let item = this.state.selectedItem;
        if (!item) {
            Modal.info({
                title: '',
                content: ''
            })
            return;
        }
        axios.ajax({
            url:'/order/ebike_info',
            data:{
                params:{
                    orderId: item.id
                }
            }
        }).then((res)=>{
            if(res.code === 0 ){
                this.setState({
                    orderInfo:res.result,
                    orderConfirmVisble: true
                })
            }
        })
    }

    // 
    handleFinishOrder = ()=>{
        let item = this.state.selectedItem;
        axios.ajax({
            url: '/order/finish_order',
            data: {
                params: {
                    orderId: item.id
                }
            }
        }).then((res) => {
            if (res.code === 0) {
                message.success('')
                this.setState({
                    orderConfirmVisble: false
                })
                this.requestList();
            }
        })
    }
    onRowClick = (record, index) => {
        let selectKey = [index];
        this.setState({
            selectedRowKeys: selectKey,
            selectedItem: record
        })
    }

    openOrderDetail = ()=>{
        let item = this.state.selectedItem;
        if (!item) {
            Modal.info({
                title: '',
                content: ''
            })
            return;
        }
        window.open(`/#/common/order/detail/${item.id}`,'_blank')
    }
    render(){
        const columns = [
            {
                title:'',
                dataIndex:'order_sn'
            },
            {
                title: '',
                dataIndex: 'bike_sn'
            },
            {
                title: '',
                dataIndex: 'user_name'
            },
            {
                title: '',
                dataIndex: 'mobile'
            },
            {
                title: '',
                dataIndex: 'distance',
                render(distance){
                    return distance/1000 + 'Km';
                }
            },
            {
                title: '',
                dataIndex: 'total_time'
            },
            {
                title: '',
                dataIndex: 'status'
            },
            {
                title: '',
                dataIndex: 'start_time'
            },
            {
                title: '',
                dataIndex: 'end_time'
            },
            {
                title: '',
                dataIndex: 'total_fee'
            },
            {
                title: '',
                dataIndex: 'user_pay'
            }
        ]
        const formItemLayout = {
            labelCol:{span:5},
            wrapperCol:{span:19}
        }
        return (
            <div>
                <Card>
                    <BaseForm formList={this.formList} filterSubmit={this.handleFilter}/>
                </Card>
                <Card style={{marginTop:10}}>
                    <Button type="primary" onClick={this.openOrderDetail}></Button>
                    <Button type="primary" style={{marginLeft:10}} onClick={this.handleConfirm}></Button>
                </Card>
                <div className="content-wrap">
                    <ETable
                        updateSelectedItem={Utils.updateSelectedItem.bind(this)}
                        columns={columns}
                        dataSource={this.state.list}
                        selectedRowKeys={this.state.selectedRowKeys}
                        selectedIds = {this.state.selectedIds}
                        selectedItem = {this.state.selectedItem}
                        rowSelection='checkbox'
                    />
                </div>
                <Modal
                    title=""
                    visible={this.state.orderConfirmVisble}
                    onCancel={()=>{
                        this.setState({
                            orderConfirmVisble:false
                        })
                    }}
                    onOk={this.handleFinishOrder}
                    width={600}
                >
                    <Form layout="horizontal">
                        <FormItem label="" {...formItemLayout}>
                            {this.state.orderInfo.bike_sn}
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {this.state.orderInfo.battery + '%'}
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {this.state.orderInfo.start_time}
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {this.state.orderInfo.location}
                        </FormItem>
                    </Form>
                </Modal>
            </div>
        );
    }
}