import React from 'react';
import { Card,Form,Input,Button,message,Icon, Checkbox} from 'antd';
import './ui.less';

const FormItem = Form.Item;
 class FormLogin extends React.Component{
    handleSubmit = ()=>{
        let useInfo = this.props.form.getFieldsValue();
        console.log(useInfo)
        this.props.form.validateFields((err,values)=>{
            if(!err){
                message.success(`${useInfo.User_name} ${useInfo.User_password}`);
            }
        })
    }
    render(){
        const { getFieldDecorator } =  this.props.form;
        return (
            <div>
                <Card title="" className="card-warp">
                    <Form layout="inline">
                        {/* <Col span={3}>
                            <FormItem>
                                <Input type="text" name="user.User_username" placeholder=""/>
                            </FormItem> 
                        </Col> */}
                        <FormItem>
                                <Input type="text"  placeholder=""/>
                        </FormItem>
                        <FormItem>
                            <Input type="password"  placeholder=""/>  
                        </FormItem>
                        <FormItem>
                            <Button type="primary"></Button>
                        </FormItem>
                    </Form>
                </Card>
                <Card title="" style={{marginTop:10}}>
                    <Form layout="horizontal" style={{width:300}}>
                        {/* <Col span={3}>
                            <FormItem>
                                <Input type="text" name="user.User_username" placeholder=""/>
                            </FormItem>
                        </Col> */}
                        <FormItem>
                                {
                                    getFieldDecorator('User_name',{
                                        rules:[
                                            {
                                                required:true,
                                                message:''
                                            },{
                                              min:5,
                                              max:10,
                                              message:""  
                                            },{
                                                pattern:/^\w+$/g,
                                                message:""
                                            }
                                        ]
                                    })(
                                        <Input type="text" prefix={<Icon type="user"/>}  placeholder=""/>
                                    )
                                }
                        </FormItem>
                        <FormItem>
                                {
                                    getFieldDecorator('User_password',{
                                        rules:[
                                            {
                                                required:true,
                                                message:''
                                            }
                                        ]
                                    })(
                                        <Input prefix={<Icon type="lock"/>} type="password"  placeholder=""/>
                                    )
                                }
                            
                        </FormItem>
                        <FormItem>
                            {
                                getFieldDecorator('remember',{
                                    valuePropName:"checked",
                                    initialValue:true
                                })(
                                    <Checkbox ></Checkbox>
                                )
                            }
                            <a href="https://baidu.com" style={{float:"right"}}></a>
                        </FormItem>
                        <FormItem> 
                            <Button type="primary" onClick={this.handleSubmit}></Button>
                        </FormItem>
                    </Form>
                </Card>
            </div>
        )
    }
}
export default Form.create()(FormLogin);