import React, { Component } from 'react';
import {Card,Table,Modal,message,Button,Badge} from 'antd';
import axios from 'axios';
// import Utils from '../../util/utils';
import '../../util/myMock'

class HighTable extends Component {
    state={
        dataSource:null
    }
    request(){
        axios.get('/wbbb').then(res=>{
            let dataSource = res.data.data;
            dataSource.map((item,index)=>{
                item.key=index;
                return item;
            })
            this.setState({
                dataSource
            })
        })
    }
    handleDelete = (item)=>{
        let id = item.id;
        Modal.confirm({
            title:"",
            content:"",
            onOk:()=>{
                message.success('',id);
            }
        })
    }
    componentDidMount(){
        this.request();
    }


    
    render() {
        const columns = [
            {
                title:"id",
                dataIndex:"id"
            },
            {
                title:"",
                dataIndex:"userName"
            },
            {
                title:"",
                dataIndex:"sex"
            },
            {
                title:"",
                dataIndex:'state'
            },
            {
                title:'',
                dataIndex:'interest'
            },
            {
                title:'',
                dataIndex:'birthday'
            },
            {
                title:'',
                dataIndex:'address'
            }
        ]
        const columns2 = [
            {
                title:"id",
                dataIndex:"id",
                key:'id',
                width:80
            },
            {
                title:"",
                dataIndex:"userName",
                width:80
            },
            {
                title:"",
                dataIndex:"sex",
                width:80
            },
            {
                title:"",
                dataIndex:'state',
                width:80
            },
            {
                title:'',
                dataIndex:'interest',
                width:80
            },
            {
                title:'',
                dataIndex:'birthday',
                width:80
            }
        ]
        const columns3 = [
            {
                title:"id",
                dataIndex:"id"
            },
            {
                title:"",
                dataIndex:"userName",
                // key:'userName',
                sorter:(a,b)=>{
                    return a.userName.length - b.userName.length;
                },
                sortDirections: ['descend', 'ascend']
            },
            {
                title:"",
                dataIndex:"sex"
            },
            {
                title:"",
                key:'state',
                dataIndex:'state',
                sorter:(a,b)=>{
                    return a.state - b.state
                },
                render:function(state){
                    let config = {
                        '1': <Badge status='success' text={''} />,
                        '2':<Badge status='success' text={''} />,
                        '3':<Badge status='success' text={''} />,
                        "4":<Badge status='success' text={'fe'} />,
                        '5':<Badge status='success' text={''} />
                    }
                    return config[state]
                }
            },
            {
                title:'',
                dataIndex:'interest'
            },
            {
                title:'',
                dataIndex:'birthday'
            },
            {
                title:'',
                render:(text,item)=>{
                    return <Button onClick={()=>{this.handleDelete(item)}}></Button>
                }
            }
        ]
        return (
            <div>
                <Card title="">
                     <Table
                      columns={columns3}
                      dataSource={this.state.dataSource}
                      scroll={{x:1600}}
                      >

                     </Table>
                </Card>
                <Card title="">
                     <Table
                      columns={columns2}
                      dataSource={this.state.dataSource}
                      >

                     </Table>
                </Card>
                
                <Card title="">
                     <Table
                      columns={columns}
                      dataSource={this.state.dataSource}
                      scroll={{y:300}}
                      >

                     </Table>
                </Card>
              
            </div>
        );
    }
}

export default HighTable;