import React from 'react';
import {Card,Form,Button,Icon,Input,Checkbox,Radio,Select,Switch,InputNumber,DatePicker,Upload,message} from 'antd';
import moment from 'moment';
const FormItem = Form.Item;
const RadioGroup = Radio.Group;
const TextArea = Input.TextArea ;   
class FormRegister extends React.Component {
    state={
        imageUrl:null
    }
    handleSubmit = ()=>{
        let useInfo = this.props.form.getFieldsValue();
        console.log(useInfo)
        this.props.form.validateFields((err,values)=>{
            if(!err){
                console.log(JSON.stringify(useInfo));
                message.success(`${useInfo.User_name} ${useInfo.User_password}`);
            }
        })
    }
    getBase64 = (img, callback) =>{
        const reader = new FileReader();
        reader.addEventListener('load', () => callback(reader.result));
        reader.readAsDataURL(img);
    }

    handleChange = info => {
        console.log(info.file);
        if (info.file.status === 'uploading') {
          this.setState({ loading: true });
          return;
        }
        if (info.file.status === 'done') {
          // Get this url from response in real world.
          this.getBase64(info.file.originFileObj, imageUrl =>{
            this.setState({
              imageUrl,
              loading: false,
            })},
          );
        }
      }
    render(){
        const Option = Select.Option
        const { getFieldDecorator } =  this.props.form;
        let useInfo = this.props.form.getFieldsValue();
        console.log(useInfo);
        // Boss,may be you can use this methods to manage layouts in other project
        const formItemLayout = {
            labelCol:{
                xs:24,
                sm:4
            },
            wrapperCol:{
                xs:24,
                sm:20
            }
        }
        const offsetLayout = {
            wrapperCol:{
                xs:24,
                sm:{
                    span:12,
                    offset:4
                }
            }
        }
        // const uploadprops = {
        //     name: 'file',
        //     action: 'https://www.mocky.io/v2/5cc8019d300000980a055e76',
        //     headers: {
        //       authorization: 'authorization-text',
        //     },
        //     onChange(info) {
        //       if (info.file.status !== 'uploading') {
        //         console.log(info.file, info.fileList);
        //       }
        //       if (info.file.status === 'done') {
        //         message.success(`${info.file.name} file uploaded successfully`);
        //       } else if (info.file.status === 'error') {
        //         message.error(`${info.file.name} file upload failed.`);
        //       }
        //     },
        // }

        return (
            <div>
                <Card title="">
                    <Form layout="horizontal">
                        <FormItem label="" {...formItemLayout}>
                        {
                            getFieldDecorator('User_name',{
                                rules:[
                                    {
                                        required:true,
                                        message:''
                                    }
                                ]
                            })(
                                <Input prefix={<Icon type="user"/>} type="text"  placeholder=""/>
                            )
                        }
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                        {
                            getFieldDecorator('User_password',{
                                rules:[
                                    {
                                        required:true,
                                        message:''
                                    }
                                ]
                            })(
                                <Input prefix={<Icon type="lock"/>} type="text"  placeholder=""/>
                            )
                        }
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                        {
                            getFieldDecorator('sex',{
                                initialValue:"1",
                                rules:[
                                    {
                                        required:true,
                                        message:''
                                    }
                                ]
                            })(
                              <RadioGroup>
                                <Radio value="1"></Radio>
                                <Radio value="2"></Radio>
                              </RadioGroup>
                            )
                        }
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                        {
                            getFieldDecorator('age',{
                                initialValue:18,
                                rules:[
                                    {
                                        required:true,
                                        message:''
                                    }
                                ]
                            })(
                              <InputNumber></InputNumber>
                            )
                        }
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                        {
                            getFieldDecorator('state',{
                                initialValue:"LV1",
                                rules:[
                                    {
                                        required:true,
                                        message:''
                                    }
                                ]
                            })(
                                <Select>
                                    <Option  key="LV1">LV1</Option>
                                    <Option  key="LV2">LV2</Option>
                                    <Option  key="LV3">LV3</Option>
                                    <Option  key="LV4">LV4</Option>
                                </Select>
                            )
                        }
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                        {
                            getFieldDecorator('hoby',{
                                initialValue:["",""],
                                rules:[
                                    {
                                        required:true,
                                        message:''
                                    }
                                ]
                            })(
                                <Select mode="multiple">
                                    <Option  key=""></Option>
                                    <Option  key=""></Option>
                                    <Option  key="RS">RS</Option>
                                    <Option  key=""></Option>
                                    <Option  key=""></Option>
                                    <Option  key=""></Option>
                                </Select>
                            )
                        }
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                        {
                            getFieldDecorator('marrage',{
                                valuePropName:"checked",
                                initialValue:true,   
                                rules:[
                                    {
                                        required:true,
                                    }
                                ]
                            })(
                                <Switch></Switch>
                            )
                        }
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {
                                getFieldDecorator('birthday',{
                                    initialValue:moment('2018-08-08 11:11:11')
                                })(<DatePicker
                                    showTime
                                    format="YYYY-MM-DD hh:mm:ss"
                                />)
                            }
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {
                                getFieldDecorator('addredss',{
                                    rules:[
                                        {
                                            required:true,
                                            message:''
                                        }
                                    ]
                                })(
                                    <TextArea
                                        autoSize={{minRows:4,maxRow:6}}
                                    />
                                )
                            }
                        </FormItem> 
                        <FormItem label="" {...formItemLayout}>
                            {
                                getFieldDecorator('useImg')(
                                    <Upload 
                                            listType="picture-card"
                                            showUploadList={false}
                                            action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                                            onChange={this.handleChange}

                                    >
                                        {this.state.imageUrl?<img src={this.state.imageUrl} alt=""/>:<Icon type="plus"/>}
                                    </Upload>
                                )
                            }
                        </FormItem>
                        <FormItem   {...offsetLayout}>
                            {
                                getFieldDecorator('agree')(
                                    <Checkbox><a href="https://baidu.com"> </a></Checkbox>
                                )
                            }
                        </FormItem> 
                        <FormItem   {...offsetLayout}>
                            <Button type="primary" onClick={this.handleSubmit}></Button>
                        </FormItem>
                    </Form>
                </Card>
            </div>
        )
    }
}

export default Form.create()(FormRegister);
