import React from 'react';
import FormItem from 'antd/lib/form/FormItem';
import {Card,Button,Table,Form, Select,Modal,message} from 'antd';
import axios from '../../axios/index';
import Utils from './../../util/utils';
const Option = Select.Option;
export default class City extends React.Component {
    state={
        list:[],
        isShowOpenCity:false
    }
    params = {
        page:1,
      
    }
    handleOpenCity = ()=>{
        this.setState({
            isShowOpenCity:true
        })
    }
    //
    handleSubmit = ()=>{
        let cityinfo = this.cityForm.props.form.getFieldsValue();
        axios.ajax({
            url:"/open_city",
            data:{
                params:cityinfo
            }
        }).then(res=>{
            if(res.code === '0'){
                message.success('');
                this.requestList();   
            }
        })
        this.setState({
            isShowOpenCity:false
        })
    }
    requestList(){
        let _this = this;
        axios.ajax({
            url:"/open_city",
            data:{
                params:{
                    page:this.params.page
                }
            }
        }).then((res)=>{
           this.setState({
               list:res.result.item_list.map((item,index)=>{
                   item.key = index;
                   return item;
               }),
               pagination:Utils.pagenation(res,(current)=>{
                   _this.params.page = current;
                   _this.requestList();
               })
           })
        })
    }



    componentDidMount(){
        this.requestList();
    }
    render(){
        const columns = [
            {
                title:"ID",
                dataIndex:"id",
            },{
                title:"",
                dataIndex:"name"
            },{
                title:"",
                dataIndex:"mode",
            },{
                title:"",
                dataIndex:"franchisee_name"
            },{
                title:"",
                dataIndex:"city_admins",
                render(arr){
                    return arr.map((item)=>{
                        return item.user_name
                    }).join(',');
                }
            },{
                title:"",
                dataIndex:"open_time"
            },{
                title:"",
                dataIndex:"update_time"
            },{
                title:"",
                dataIndex:"sys_user_name"
            }
        ]
        return (
            <div>
                <Card>
                    <FilterForm/>    
                </Card>
                <Card style={{marginTop:10}}>
                    <Button type="primary" onClick={this.handleOpenCity}></Button>
                </Card>
                <div className="content-warp">
                    <Table 
                    columns={columns}
                    dataSource={this.state.list}
                    />
                </div>
                <Modal
                 title=""
                 visible={this.state.isShowOpenCity}
                 onCancel={()=>{
                     this.setState({
                         isShowOpenCity:false
                     })
                 }}
                 onOk={this.handleSubmit}
                 >
                <OpenCityForm wrappedComponentRef={(inst)=>{this.cityForm = inst }}/>
                </Modal>
            </div>
        )
    }
}
class FilterForm extends React.Component {
    render(){
       const {getFieldDecorator} = this.props.form;
       return (
        <Form layout="inline">
            <FormItem label="">
                {
                    getFieldDecorator('city_id')(
                        <Select placeholder="" style={{width:"80px"}}>
                            <Option value="1"></Option>
                            <Option value="2"></Option>
                            <Option value="3"></Option>
                        </Select>
                    )
                }
            </FormItem>
            <FormItem label="">
                {
                    getFieldDecorator('model')(
                        <Select placeholder="" style={{width:"120px"}}>
                            <Option value=""></Option>
                            <Option value="1"></Option>
                            <Option value="2"></Option>
                        </Select>
                    )
                }
            </FormItem>
            <FormItem label="">
                {
                    getFieldDecorator('op_mode')(
                        <Select placeholder="" style={{width:"80px"}}>
                            <Option value=""></Option>
                            <Option value="1"></Option>
                            <Option value="2"></Option>
                        </Select>
                    )
                }
            </FormItem>
            <FormItem label="">
                {
                    getFieldDecorator('op_mode')(
                        <Select placeholder="" style={{width:"100px"}}>
                            <Option value=""></Option>
                            <Option value="1"></Option>
                            <Option value="2"></Option>
                        </Select>
                    )
                }
            </FormItem>
            <FormItem>
                <Button type="primary" style={{margin:"0 20px"}}></Button>
                <Button></Button>
            </FormItem>
        </Form>
       )
    }
}
FilterForm = Form.create({})(FilterForm);

class OpenCityForm extends React.Component {
    
    render(){
        const FormItemLayout  = {
            labelCol:{
                span:5
            },
            wrapperCol:{
                span:10
            }
        }
        const {getFieldDecorator} = this.props.form ;
        return (
            <Form layout="horizontal">
                <FormItem label="" {...FormItemLayout}>
                {
                    getFieldDecorator('city_id',{
                        initialValue:"1"
                    })(
                        <Select placeholder="" style={{width:"100px"}}>
                            <Option value="0"></Option>
                            <Option value="1"></Option>
                            <Option value="2"></Option>
                        </Select>
                    )
                }
                </FormItem>
                <FormItem label="" {...FormItemLayout}>
                {
                    getFieldDecorator('op_mode',{
                        initialValue:"1"
                    })(
                        <Select  style={{width:"100px"}}>
                            <Option value="1"></Option>
                            <Option value="2"></Option>
                        </Select>
                    )
                }
                   
                </FormItem>
                <FormItem label="" {...FormItemLayout}>
                {
                    getFieldDecorator('use_mode',{
                        initialValue:"1"
                    })(
                        <Select  style={{width:"100px"}}>
                            <Option value="1"></Option>
                            <Option value="2"></Option>
                        </Select>
                    )
                }
                </FormItem>
            </Form>
        )
    }
}
OpenCityForm = Form.create({})(OpenCityForm);
