import React, { Component } from 'react';
import './ui.less'
import { Card,Button,Modal } from 'antd';
class Model extends Component {
    constructor(){
        super()
        this.state={
            showModel1:false,
            showModel2:false,
            showModel3:false,
            showModel4:false
        }
    }
    handleOpen = (type)=>{
        console.log(type)
            this.setState({
                [type]:true  
            })
    }
    handleOpen = (type)=>{
        Modal[type]({
            titile:"",
            content:"",
            onOk(){
                console.log('ok')
            },
            onCancel(){
                console.log('cancle')
            }
        })
    }
    render() {
        return (
            <div>
                <Card title="" className="card-warp">
                    <Button type="primary" onClick={()=>this.handleOpen('showModel1')}>Open</Button>
                    <Button onClick={()=>this.handleOpen('showModel2')}></Button>
                    <Button onClick={()=>this.handleOpen('showModel3')}>20px</Button>
                    <Button onClick={()=>this.handleOpen('showModel4')}></Button>
                </Card>
                <Card title="" className="card-warp">
                    <Button type="primary" onClick={()=>this.handleOpen('confirm')}> </Button>
                    <Button onClick={()=>this.handleOpen('info')}></Button>
                    <Button onClick={()=>this.handleOpen('success')}></Button>
                    <Button onClick={()=>this.handleOpen('warning')}> </Button>
                </Card>
                <Modal 
                    title="React"
                    visible={this.state.showModel1}
                    onCancel={()=>{
                        this.setState({
                            showModel1:false
                        })
                    }}
                >
                    <span></span>
                </Modal>
                <Modal 
                    title="React"
                    visible={this.state.showModel2}
                    okText=""
                    cancelText=""
                    onCancel={()=>{
                        this.setState({
                            showModel2:false
                        })
                    }}
                    onOk={()=>{
                        this.setState({
                            showModel2:false
                        })
                    }}
                >
                    <span></span>
                </Modal>
                <Modal 
                    title="React"
                    visible={this.state.showModel3}
                    style={{top:20}}
                    okText=""
                    cancelText=""
                    onCancel={()=>{
                        this.setState({
                            showModel3:false
                        })
                    }}
                >
                    <span></span>
                </Modal>
                <Modal 
                    title="React"
                    visible={this.state.showModel4}
                    wrapClassName="vertical-center-modal"
                    onCancel={()=>{
                        this.setState({
                            showModel4:false
                        })
                    }}
                >
                    <span></span>
                </Modal>
            </div>
        );
    }
}

export default Model;