import React from 'react';
import {Card} from 'antd';

//
// import echarts from  'echarts/lib/echarts'

// import 'echarts/lib/chart/bar';
// import 'echarts/lib/component/tooltip'
// import 'echarts/lib/component/title';
// import 'echarts/lib/component/legend';
// import 'echarts/lib/component/markPoint';


import ReactEcharts from 'echarts-for-react';

export default class Bar extends React.Component {
    getOption = ()=>{
        let option = {
            title:{
                text:'',
                x:'center'
            },
            tooltip:{
                trigger:'item',
                formatter:'{a}<br/>{b}:{c}({d}%)'
            },  
            legend:{
                top:10,
                right:20,
                orient:'vertical',
                data:['','','','','','','']
            },
            series:[
                {
                    name:'',
                    type:'pie',
                    data:[
                        {
                            value:2100,
                            name:''
                        },{
                            value:1700,
                            name:''
                        },{
                            value:1100,
                            name:''
                        },{
                            value:2000,
                            name:''
                        },{
                            value:4500,
                            name:''
                        },{
                            value:3000,
                            name:''
                        },{
                            value:2000,
                            name:''
                        }
                    ]
                }
            ]
        }
        return option;
    }
    getOption2 = ()=>{
        let option = {
            title:{
                text:'',
                x:'center'
            },
            tooltip:{
                trigger:'item',
                formatter:'{a}<br/>{b}:{c}({d}%)'
            },  
            legend:{
                top:10,
                right:20,
                orient:'vertical',
                data:['','','','','','','']
            },
            series:[
                {
                    name:'',
                    type:'pie',
                    roseType: 'radius',
                    data:[
                        {
                            value:2100,
                            name:''
                        },{
                            value:1700,
                            name:''
                        },{
                            value:1100,
                            name:''
                        },{
                            value:2000,
                            name:''
                        },{
                            value:4500,
                            name:''
                        },{
                            value:3000,
                            name:''
                        },{
                            value:2000,
                            name:''
                        }
                    ].sort((a,b)=>{return a.value - b.value}),
                    animationType: 'scale',
                    animationEasing: 'elasticOut',
                    animationDelay: function (idx) {
                        return Math.random() * 200;
                    }
                }
            ]
        }
        return option;
    }
    render(){
        return (
            <div>
                <Card title="" style={{marginTop:10}}>
                    <ReactEcharts
                        option = {this.getOption2()}
                        style={{height:600}}
                    />
                </Card>
                <Card title="">
                    <ReactEcharts
                        option = {this.getOption()}
                        style={{height:600}}
                    />
                </Card>
            </div>
        )
    }
}
