import React from 'react';
import {Button,Card, Modal, Form, Input, Radio, Select, DatePicker} from 'antd';
import axios from './../../axios';
import Utils from '../../util/utils';
import BaseForm from '../../components/BaseForm';
import ETable from '../../components/ETable';
import moment from 'moment';
const RadioGroup = Radio.Group;
const FormItem = Form.Item;
const TextArea = Input.TextArea;
const Option = Select.Option;

export default class User extends React.Component {
    params = {
        page:1
    }
    state = {
        isVisible:false
    }
    formList = [
        {
            type:'INPUT',
            label:"",
            field:'user_name',
            placeholder:'',
            width:140
        },{
            type:'INPUT',
            label:"",
            field:'user_mobile',
            placeholder:'',
            width:130
        },{
            type:'DATEPICKER',
            label:'',
            field:'user_data',
            placeholder:''
        }
    ]

    componentDidMount(){
        this.requestList();
    }
    handleFilter = (params)=>{
        this.params = params
        this.requestList();
    }
    hadleOperate = (type)=>{
        console.log(type)
        let item = this.state.selectedItem;
        if(type === 'create'){
            this.setState({
                type,
                isVisible:true,
                title:''
            })
        
        }else if(type === 'edit'){
            if(!item){
                Modal.info({
                    title:"",
                    content:""
                })
                return ;
            }
            this.setState({
                type,
                isVisible:true,
                title:'',
                userInfo:item
            })
        }else if(type === 'detail'){
            if(!item){
                Modal.info({
                    title:"",
                    content:""
                })
                return ;
            }
            this.setState({
                type,
                isVisible:true,
                title:'',
                userInfo:item
            })
        }else{
            if(!item){
                Modal.info({
                    title:"",
                    content:""
                })
                return ;
            }
            Modal.confirm({
                title:'',
                content:'',
                onOk:()=>{
                    axios.ajax({
                        url:'/user/delete',
                        data:{
                            param:{
                                id:item.id
                            }
                        }
                    }).then(res=>{
                        if(res.code === 0 ){
                            this.setState({
                                isVisible:false
                            })
                        }
                        this.requestList();
                    })
                }
            })
        }
       
    }
    // commit the created Form
    handleSubmit = ()=>{
        let type = this.state.type;
        let data = this.UserForm.props.form.getFieldsValue();
        axios.ajax({
            url:type ==='create'?'/user/add':'/user/edit',
            data:{
                params:data
            }
        }).then(res=>{
            if(res.code === '0'){
                this.setState({
                    isVisible:false
                });
                this.requestList();
            }
        })

    }
    requestList = ()=>{
        axios.requestList(this,'/user/list',this.params,true);
    }
   

    render(){
        const columns = [
            {
                title:"id",
                dataIndex:'id'
            },{
                title:"",
                dataIndex:'username'
            },{
                title:"",
                dataIndex:'sex',
                render(sex) {
                    return sex === 1?'':''
                }
            },{
                title:"",
                dataIndex:'state',
                //this is a very lit way,boss
                render(state){
                    return {
                        '1':'',
                        '2':'',
                        '3':'',
                        '4':'FE',
                        '5':''
                    }[state]
                }
            },{
                title:"",
                dataIndex:'interest',
                render(state){
                    return {
                        '1':'',
                        '2':'',
                        '3':'',
                        '4':'',
                        '5':'',
                        '6':'',
                        '7':'',
                        '8':''
                    }[state]
                }
            },{
                title:"",
                dataIndex:'birthday'
            },{
                title:"",
                dataIndex:'address'
            },{
                title:"",
                dataIndex:'time'
            }
        ]
        let footer = {};
        if(this.state.type === 'detail'){
            footer = {
                footer:null
            }
        }
        return (
            <div>
                <Card>
                    <BaseForm formList={this.formList} filterSubmit={this.handleFilter}/>
                </Card>
                <Card style={{marginTop:10}} className="operate-wrap">
                    <Button type="primary" icon='plus' onClick={()=>{this.hadleOperate('create')}}></Button>
                    <Button type="primary" icon='edit' onClick={()=>this.hadleOperate('edit')}></Button>
                    <Button type="primary" icon='plus' onClick={()=>this.hadleOperate('detail')}></Button>
                    <Button type='danger' icon='delete' onClick={()=>this.hadleOperate('delete')}></Button>
                    
                </Card>
                <div className="content-wrap">
                    <ETable
                        updateSelectedItem={Utils.updateSelectedItem.bind(this)}
                        columns={columns}
                        dataSource={this.state.list}
                        selectedRowKeys={this.state.selectedRowKeys}
                        rowSelection='radio'
                    />
                </div>
                <Modal
                    title={this.state.title}
                    visible={this.state.isVisible}
                    onOk={this.handleSubmit}
                    onCancel={()=>{
                        this.UserForm.props.form.resetFields();
                        this.setState({
                            isVisible:false
                        })
                      
                    }}
                    width={600}
                    {...footer}
                >
                    <UserForm type={this.state.type} userInfo={this.state.userInfo} wrappedComponentRef={(inst)=>{this.UserForm = inst}}/>
                </Modal>
            </div>
        )
    }
}

class UserForm extends React.Component {
    getState = (state)=>{
        return {
            '1':'',
            '2':'',
            '3':'',
            '4':'FE',
            '5':''
        }[state]
    }
    render(){
        let {type,userInfo}  = this.props;
        if(userInfo || !(type ==='create')){
            userInfo = userInfo[0];
        }else{
            userInfo = {}
        }
   
        // let userInfo = this.props.userInfo;
        
        // let type = this.props.type;
        const { getFieldDecorator } =  this.props.form || {};
        const formlayout = {
            labelCol:{span:5},
            wrapperCol:{span:19}
        }
        return (
            <Form layout='horizontal'>
                <FormItem label="" {...formlayout}>
                    {
                        type === 'detail'?userInfo.username:
                        getFieldDecorator('user_name',{
                            initialValue:userInfo.username||''
                        })(
                            <Input type='text' placeholder='' />
                        )
                    }
                </FormItem>
                <FormItem label="" {...formlayout}>
                    {
                         type === 'detail'?(userInfo.sex === 1 ?'':''):
                        getFieldDecorator('sex',{
                            initialValue:userInfo.sex||''
                        })(
                            <RadioGroup>
                                <Radio value={1}></Radio>
                                <Radio value={2}></Radio>
                            </RadioGroup>
                        )
                    }
                </FormItem>
                <FormItem label="" {...formlayout}>
                    {
                        type === 'detail'?this.getState(userInfo.state):
                        getFieldDecorator('state',{
                            initialValue:userInfo.state||''
                        })(
                            <Select>
                                <Option value={1}></Option>
                                <Option value={2}></Option>
                                <Option value={3}></Option>
                                <Option value={4}>FE</Option>
                                <Option value={5}></Option>
                            </Select>
                        )
                    }
                </FormItem>
                <FormItem label="" {...formlayout}>
                    {
                         type === 'detail'?userInfo.birthday:
                        getFieldDecorator('birthday',{
                            initialValue:moment(userInfo.birthday)||''
                        })(
                            <DatePicker/>
                        )
                    }
                </FormItem>
                <FormItem label="" {...formlayout}>
                    {
                         type === 'detail'?userInfo.address:
                        getFieldDecorator('address',{
                            initialValue:userInfo.address||''
                        })(
                            <TextArea rows={3} placeholder=''/>
                        )
                    }
                </FormItem>
            </Form>
        )
    }
}

UserForm = Form.create({})(UserForm)