import {Button,Card,Radio} from 'antd';
import './ui.less';
import React, { Component } from 'react';

class Buttons extends Component {
    constructor(){
        super()
        this.state = {
            loading:false,
            size:'small'
        }
        this.handleCloseLoading = this.handleCloseLoading.bind(this)
        this.handleChange = this.handleChange.bind(this)
    }
    handleCloseLoading(){
        let { loading } = this.state;
        this.setState({
            loading:!loading
        })
    }
    handleChange(e){
        this.setState({
            size:e.target.value
        })
    }
    render() {
        let {loading} = this.state;
        return (
        <div>
            <Card title="" className="card-warp">
                <Button type="primary"></Button>
                <Button type="danger"></Button>
                <Button type="dashed"></Button>
                <Button loading></Button>
            </Card>
            <Card title="" className="card-warp">
                <Button icon="plus"></Button>
                <Button icon="edit"></Button>
                <Button icon="delete"></Button>
                <Button shape="circle" icon="search"></Button>
                <Button type="primary" icon="search"></Button>
                <Button type="primary" icon="download"></Button>
            </Card>
            <Card title="Loading" className="card-warp">
                <Button icon="plus" loading={loading}></Button>
                <Button icon="edit" loading={loading}></Button>
                <Button icon="delete" loading={loading}></Button>
                <Button shape="circle" icon="search" loading={loading}></Button>
                <Button type="primary" icon="search" loading={loading}></Button>
                <Button type="primary"  onClick={this.handleCloseLoading}></Button>
            </Card>
            <Card title="" className="card-warp">
                <Button.Group>
                     <Button icon="left"></Button>
                     <Button icon="right"></Button>
                </Button.Group>
            </Card>
            <Card title="" className="card-warp">
                <Radio.Group value={this.state.size} onChange={this.handleChange}>
                    <Radio value="small"></Radio>
                    <Radio value="default"></Radio>
                    <Radio value="large"></Radio>
                </Radio.Group>
                <Button type="primary" size={this.state.size}>Imoc</Button>
                <Button type="dashed" size={this.state.size}>Imoc</Button>
                <Button type="danger" size={this.state.size}>Imoc</Button>
            </Card>
        </div> 
        );
    }
}

export default Buttons;
