import React, { Component } from 'react';
import {Card,Table, Modal, Button, message} from 'antd';
import axios from './../../axios';
import Utils from '../../util/utils';

class BasicTable extends Component {
    state={
        dataSource:null,
        dataSource1:null
    }
    request(){
       axios.ajax({
           url:"/tablelist",
           data:{
               params:{
                   page:1,
                   isShowLoading:true
               }
           }
       }).then(res=>{
           if(res.code === 0){
               this.setState({
                   dataSource1:res.result
               })
           }
       })
    }
    componentDidMount(){
        let _this = this;
        let dataSource = [
            {
                id:'0',
                userName:'Jack',
                sex:'1',
                state:'1',
                interest:'1',
                birthday:'2000-1-1',
                address:'',
                time:'2000'
            },
            {
                id:'0',
                userName:'Jack',
                sex:'1',
                state:'1',
                interest:'1',
                birthday:'2000-1-1',
                address:'',
                time:'2000'
            },
            {
                id:'0',
                userName:'Jack',
                sex:'1',
                state:'1',
                interest:'1',
                birthday:'2000-1-1',
                address:'',
                time:'2000'
            },
            {
                id:'0',
                userName:'Jack',
                sex:'1',
                state:'1',
                interest:'1',
                birthday:'2000-1-1',
                address:'',
                time:'2000'
            },
            {
                id:'0',
                userName:'Jack',
                sex:'1',
                state:'1',
                interest:'1',
                birthday:'2000-1-1',
                address:'',
                time:'2000'
            }
        ];
        let Hdata = {
            result:{
                dataSource,
                "page":5,
                "page_size":100,
                "total":1000
            }
        }
        dataSource.map((item,index)=>{
            item.key=index;
            return item;
        })
        this.setState({
            dataSource,
            pagenation:Utils.pagenation(Hdata,(current)=>{
                _this.params.page = current;
            })
        })
        // this.request();
    }
    onRowClick(record,index){
        let selectKey=[index];
        Modal.info({
            title:"",
            content:`${record.userName} ,${record.interest}`
        })
        this.setState({
            selectedRowKeys:selectKey,
            selectItem:record
        })
    }
    handleDelete(){
        let rows = this.state.selectedRows;
        let ids = [];
        rows.map((item)=>{
            ids.push(item.id);
            return item;
        })
        Modal.confirm({
            title:"",
            content:`${ids.join('')}`,
            onOk:()=>{
                message.success('')
                this.setState({
                    selectedRowKeys:[],
                    selectedRows:null
                })
            }
        })
    }
    render() {
        const columns = [
            {
                title:"id",
                dataIndex:"id"
            },
            {
                title:"",
                dataIndex:"userName"
            },
            {
                title:"",
                dataIndex:"sex"
            },
            {
                title:"",
                dataIndex:'state'
            },
            {
                title:'',
                dataIndex:'interest'
            },
            {
                title:'',
                dataIndex:'birthday'
            },
            {
                title:'',
                dataIndex:'address'
            },
            {
                title:'',
                dataIndex:"time"
            }
        ]
        const rowSelection = {
            type:"radio",
            selectedRowKeys:this.state.selectedRowKeys
        }
        const rowCheckSelection = {
            type:"Checkbox",
            selectedRowKeys:this.state.selectedRowKeys,
            onChange:(selectedRowKeys,selectedRows)=>{
                this.setState({
                    selectedRowKeys,
                    selectedRows
                })
                console.log(this.state)
            }
        }
        return (
            <div>
                <Card>
                     <Table
                      columns={columns}
                      dataSource={this.state.dataSource}
                      >

                     </Table>
                </Card>
                <Card title="mock">
                     <Table
                      columns={columns}
                      dataSource={this.state.dataSource1}
                      loading={!(this.state.dataSource1)}
                      >
                     </Table>
                </Card>
                <Card title="">
                     <Table
                      bordered
                      columns={columns}
                      dataSource={this.state.dataSource}
                      loading={!(this.state.dataSource)}
                      rowSelection={rowSelection}
                      onRow={(record,index)=>{
                          return {
                              onClick:()=>{
                                  this.onRowClick(record,index)
                              }
                          }
                      }}
                      >
                     </Table>
                </Card>
                <Card title="">
                    <div>
                        <Button onClick={()=>{this.handleDelete()}}>
                            
                        </Button>
                    </div>
                     <Table
                      bordered
                      columns={columns}
                      dataSource={this.state.dataSource}
                      loading={!(this.state.dataSource)}
                      rowSelection={rowCheckSelection}
                      >
                     </Table>
                </Card>
                <Card title="">
                     <Table
                      bordered
                      columns={columns}
                      dataSource={this.state.dataSource}
                      loading={!(this.state.dataSource)}
                      rowSelection={rowCheckSelection}
                      pagination={this.state.pagenation}
                      >
                     </Table>
                </Card>
            </div>
        );
    }
}

export default BasicTable;